#box > #section4 {
  /* background-color: blue; */
}
@keyframes change {
  0% {
    background: red;
  }
  100% {
    background: blue;
  }
}
#section4 .circle {
  width: 30vw;
  height: 30vw;
  border-radius: 50%;
  border: 1px solid red;
}
#section4 .circle.active {
  animation: change 1s infinite alternate linear;
}
